import React, {useContext, useEffect, useState} from 'react';
import {withRouter} from 'react-router-dom';
import instance from '../apis/chip';
import LoadGif from "../assets/img/load.gif";
import ToLink from "./ToLink";
import ToBack from "./ToBack";
import LogoView from "./LogoView";
import {ModelContext, TitleContext, BreadContext} from "../Store";
import Bread from "./Bread";


function years({match}) {
    const [years, setYears] = useState([]);
    const [, setTitle] = useContext(TitleContext);
    const [model, setModel] = useContext(ModelContext);
    const [, setBread] = useContext(BreadContext)


    function Items() {
        if (years.length < 1) {
            return (
                <img src={LoadGif} alt={'Loading'}/>
            )
        } else {

            return years.map((data) =>
                <ToLink key={data.id}
                        to={`/chiptuning/${data.seo_url}`}
                        title={data.var_title}/>
            )
        }
    }

    useEffect(() => {
        instance
            .get(`/chiptuning/${match.params.brandId}/${match.params.modelId}`)
            .then(result => {
                setYears(result.data)
                const modelData = result.data[0].parent
                const brandData = modelData.parent
                setTitle(modelData.seo_title)
                setModel(modelData)
                setBread([
                    {
                        id: 2,
                        icon: 'copyright',
                        link: `/chiptuning/${modelData.seo_url}`,
                        title: modelData.var_title
                    },
                    {
                        id: 1,
                        icon: 'angle-double-right',
                        link: `/chiptuning/${brandData.seo_url}`,
                        title: brandData.var_title
                    }
                ])
            });
    }, []);

    return (
        <div>
            <Bread/>
            <LogoView brandId={match.params.brandId}/>
            <ToBack  to={`/chiptuning/${match.params.brandId}`} title={model.var_title}/>
            <Items/>
        </div>
    );
}

export default withRouter(years);