import React, {useContext, useEffect, useState} from 'react';
import {withRouter} from 'react-router-dom';
import Circle from 'react-circle';
import instance from '../apis/chip';
import ContentLoader from "react-content-loader"
import ToLink from "./ToLink";
import ToBack from "./ToBack";
import LogoView from "./LogoView";
import {BreadContext, EngineContext, EnginesContext, LangContext, ModelContext, TitleContext, YearContext} from "../Store";
import Bread from "./Bread";

function Stagesdata({match}) {
    const [engines, setEngines] = useContext(EnginesContext);
    const [stage, setStage] = useState({});
    const [stageId, setStageId] = useState(null);
    const [stages, setStages] = useState([]);
    const [engineId, setEngineId] = useState(match.params.engineId);
    const [lang] = useContext(LangContext)
    const [, setTitle] = useContext(TitleContext);
    const [year, setYear] = useContext(YearContext);
    const [, setModel] = useContext(ModelContext);
    const [, setEngine] = useContext(EngineContext);
    const [, setBread] = useContext(BreadContext)


    function Stage() {
        if (stage == null) {
            return '';
        }
        const pageSize = window.screen.availWidth;

        if (pageSize < 1000) {
            window.scrollTo({top: 200, left: 0, behavior: 'smooth'});
        }

        const HeaderStage = () => {
            return stages.map((stageData) => (
                <div key={stageData.id} onClick={() => cstStages(stageData.id)}
                     className={`cst-stages-header-item acr-border ${stage.id === stageData.id ? 'cst-item-text-active' : ''}`}>
                    {stageData.var_title.toUpperCase()}
                </div>
            ))
        }

        function HeaderLeftDiv() {
            if (pageSize < 1000) {
                return '';
            }
            try {
                if (stage.stage_type === 0) {
                    return (
                        <div id="cst-left-header-div" className="cst-stage-item">
                            <div id="cst-stage-header" className="cst-stage-item-row cst-stage-item-title ">
                                <div id="cst-stage-engine" className="cst-stage-item-text-text"></div>
                            </div>
                            <div className="cst-stage-item-row cst-stage-item-title-left">
                                <div className="cst-stage-item-text-text">{lang.TORQUE} (Nm)</div>
                            </div>
                        </div>
                    )
                } else {
                    return (
                        <div id="cst-left-header-div" className="cst-stage-item">
                            <div id="cst-stage-header" className="cst-stage-item-row cst-stage-item-title ">
                                <div id="cst-stage-engine" className="cst-stage-item-text-text"></div>
                            </div>
                            <div className="cst-stage-item-row cst-stage-item-title-left">
                                <div className="cst-stage-item-text-text">{lang.POWER} (Hp)</div>
                            </div>
                            <div className="cst-stage-item-row cst-stage-item-title-left">
                                <div className="cst-stage-item-text-text">{lang.TORQUE} (Nm)</div>
                            </div>
                        </div>
                    )
                }

            } catch (e) {
                return (<></>)
            }

        }

        try {
            return (
                <div id="cst-stage-row">
                    <div className="cst-stages-header">
                        <HeaderStage/>
                    </div>
                    <div id="cst-stage">
                        <HeaderLeftDiv/>
                        <StageContent/>
                    </div>
                </div>
            )
        } catch (e) {
            return (<></>)
        }
    }

    const StageContent = () => {
        if (stage.stage_type === 0) {
            return (
                <>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.ORIGINALS} </div>
                        </div>

                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div id="cst-nm" className="cst-stage-item-text-text">{stage.int_nm_org}Nm</div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.AFTER}</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="cst-nm" className="nmTun">{stage.int_nm_new}Nm</div>
                                <div id="cst-nm-diff" className="cst-stage-item-text-diff"></div>
                            </div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.DÄ°FFRENT}</div>
                        </div>

                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="circleNm" className="cst-circle">
                                    <strong data-after="nm">
                                        +{stage.int_nm_diff}Nm
                                    </strong>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.FUEL}</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-last">
                            <div className="cst-stage-item-text-text">
                                <div id="circleEco">
                                    <strong>
                                        <ToCircle eco={stage.int_eco == null ? 0 : stage.int_eco}/>
                                    </strong>
                                </div>
                            </div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-last">
                            <div className="cst-stage-item-text-text">
                                <strong>
                                    {stage.price}â¬
                                </strong>
                            </div>
                        </div>
                    </div>
                </>
            )
        } else {
            return (
                <>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.ORIGINALS} </div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div id="cst-hp" className="cst-stage-item-text-text">{stage.int_hp_org}Hp</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div id="cst-nm" className="cst-stage-item-text-text">{stage.int_nm_org}Nm</div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.AFTER}</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="cst-hp" className="hpTun">{stage.int_hp_new}Hp</div>
                                <div id="cst-hp-diff" className="cst-stage-item-text-diff"></div>
                            </div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="cst-nm" className="nmTun">{stage.int_nm_new}Nm</div>
                                <div id="cst-nm-diff" className="cst-stage-item-text-diff"></div>
                            </div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.DÄ°FFRENT}</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="circleHp" className="cst-circle">
                                    <strong data-after="hp">+{stage.int_hp_diff}Hp</strong>
                                </div>

                            </div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-text">
                            <div className="cst-stage-item-text-text">
                                <div id="circleNm" className="cst-circle">
                                    <strong data-after="nm">
                                        +{stage.int_nm_diff}Nm
                                    </strong>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="cst-stage-item">
                        <div className="cst-stage-item-row cst-stage-item-title">
                            <div className="cst-stage-item-text-text">{lang.FUEL}</div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-last">
                            <div className="cst-stage-item-text-text">
                                <div id="circleEco">
                                    <strong>
                                        <ToCircle eco={stage.int_eco == null ? 0 : stage.int_eco}/>
                                    </strong>
                                </div>
                            </div>
                        </div>
                        <div className="cst-stage-item-row cst-stage-item-last">
                            <div className="cst-stage-item-text-text">
                                <strong>
                                    {stage.price}â¬
                                </strong>
                            </div>
                        </div>
                    </div>
                </>
            )
        }

    }

    function ToCircle(props) {
        return (<Circle
            animationDuration={'3s'} // String: Length of animation
            responsive={false} // Boolean: Make SVG adapt to parent size
            size="100" // String: Defines the size of the circle.
            lineWidth="25" // String: Defines the thickness of the circle's stroke.
            progress={props.eco} // String: Update to change the progress and percentage.
            progressColor="rgb(76, 154, 255)" // String: Color of "progress" portion of circle.
            bgColor="#ecedf0" // String: Color of "empty" portion of circle.
            textColor="#6b778c" // String: Color of percentage text color.
            percentSpacing={10} // Number: Adjust spacing of "%" symbol and number.
            roundedStroke={false} // Boolean: Rounded/Flat line ends
            showPercentage={true} // Boolean: Show/hide percentage.
            showPercentageSymbol={true} // Boolean: Show/hide only the "%" symbol.
            animate={true} // Boolean: Animated/Static progress

        />)

    }

    function cstStages(id) {
        setStageId(id)
    }

    useEffect(() => {
        if (stageId != null) {
            instance
                .get(`/tuning/stage/${stageId}`)
                .then(result => {
                    setStage(result.data)
                });
        }
    }, [stageId]);
    useEffect(() => {
        instance
            .get(`/chiptuning/${match.params.brandId}/${match.params.modelId}/${match.params.yearId}/${engineId}`)
            .then(result => {
                if (result.data[1]) {
                    setStage(result.data[1])
                }else {
                    setStage(result.data[0])
                }
                setStages(result.data)
                const engineData = result.data[0].parent
                const yearData = engineData.parent
                const modelData = yearData.parent
                const brandData = modelData.parent
                setTitle(yearData.seo_title)
                setYear(yearData)
                setModel(modelData)
                setEngine(engineData)
                setBread([
                    {
                        id: 4,
                        icon: 'cog',
                        link: `/chiptuning/${engineData.seo_url}`,
                        title: engineData.var_title
                    },
                    {
                        id: 3,
                        icon: 'calendar-alt',
                        link: `/chiptuning/${yearData.seo_url}`,
                        title: yearData.var_title
                    },
                    {
                        id: 2,
                        icon: 'copyright',
                        link: `/chiptuning/${modelData.seo_url}`,
                        title: modelData.var_title
                    },
                    {
                        id: 1,
                        icon: 'angle-double-right',
                        link: `/chiptuning/${brandData.seo_url}`,
                        title: brandData.var_title
                    }
                ])
            });
        if (engines.length < 1) {
            instance
                .get(`/chiptuning/${match.params.brandId}/${match.params.modelId}/${match.params.yearId}`)
                .then(result => {
                    setEngines(result.data)
                })
        }

    }, [engineId]);

    function getStage(id) {
        setEngineId(id)
    }

    function Items() {
        const MyLoader = props => (
            <ContentLoader
                height={400}
                width={320}
                speed={2}
                primaryColor="rgba(69, 170, 242, .1)"
                secondaryColor="#ecebeb"
                {...props}
            >
                <rect x="5" y="0" rx="5" ry="5" className={'acr-border'} width="100%" height="30"/>
                <rect x="5" y="32" rx="5" ry="5" width="80%" height="37.86"/>
                <rect x="5" y="72" rx="5" ry="5" width="80%" height="37.86"/>
                <rect x="5" y="112" rx="5" ry="5" width="80%" height="37.86"/>
                <rect x="5" y="152" rx="5" ry="5" width="80%" height="37.86"/>
            </ContentLoader>
        )
        if (engines.length < 1) {
            return (
                <MyLoader/>
            )
        } else {

            return engines.map((data) =>
                <ToLink
                    click={() => getStage(data.id)}
                    id={`css_item_text_${data.id}`}
                    key={data.id}
                    to={`/chiptuning/${match.params.brandId}/${match.params.modelId}/${match.params.yearId}/${data.id}`}
                    active={data.id == match.params.engineId ? true : ''}
                    title={`${data.var_title} (${data.int_hp}Hp)`}/>
            )
        }
    }

    return (
        <div>
            <Bread/>
            <LogoView brandId={match.params.brandId}/>
            <div id={'cst-content-body'}>
                <div className={'cst-item'}>
                    <ToBack to={`/chiptuning/${match.params.brandId}/${match.params.modelId}/${match.params.yearId}`} title={year.var_title}/>
                    <Items/>
                </div>
                <div className={'cst-full-width acr-pl-15'}>
                    <Stage/>
                </div>
            </div>
        </div>
    );
}

export default withRouter(Stagesdata);