import React, {useState} from 'react';
export const LogoDataContext = React.createContext();
export const BrandIdContext = React.createContext();
export const ModelsContext = React.createContext();
export const ModelContext = React.createContext();
export const TitleContext = React.createContext();
export const EngineContext = React.createContext();
export const YearContext = React.createContext();
export const BreadContext = React.createContext();
export const LangContext = React.createContext();
export const EnginesContext = React.createContext();
const Store = ({children}) => {
    const [logo, setLogo] = useState(null)
    const [lang, setLang] = useState({})
    const [brandId, setBrandId] = useState(null)
    const [models, setModels] = useState([])
    const [model, setModel] = useState({seo_title: ''})
    const [year, setYear] = useState({seo_title: ''})
    const [engine, setEngine] = useState({seo_title: ''})
    const [title, setTitle] = useState('Chipt Tuning')
    const [bread, setBread] = useState([])
    const [engines, setEngines] = useState([])
    return (
        <LangContext.Provider value={[lang, setLang]}>
            <BrandIdContext.Provider value={[brandId, setBrandId]}>
                <ModelsContext.Provider value={[models, setModels]}>
                    <BreadContext.Provider value={[bread, setBread]}>
                        <ModelContext.Provider value={[model, setModel]}>
                            <YearContext.Provider value={[year, setYear]}>
                                <EngineContext.Provider value={[engine, setEngine]}>
                                    <EnginesContext.Provider value={[engines, setEngines]}>
                                        <LogoDataContext.Provider value={[logo, setLogo]}>
                                            <TitleContext.Provider value={[title, setTitle]}>
                                                {children}
                                            </TitleContext.Provider>
                                        </LogoDataContext.Provider>
                                    </EnginesContext.Provider>
                                </EngineContext.Provider>
                            </YearContext.Provider>
                        </ModelContext.Provider>
                    </BreadContext.Provider>
                </ModelsContext.Provider>
            </BrandIdContext.Provider>
        </LangContext.Provider>
    )
}
export default Store