import React, {useState, useEffect} from 'react';
import {Link} from "react-router-dom";
import {withRouter} from 'react-router-dom';
import instance from '../apis/chip';


function Main() {
    const [logos, setLogos] = useState([]);

    function CstLogos() {
        return logos.map((data, key) =>
            <div id={data.id} key={key} className={'cst-brands cst-brand-item cst-item-element cst-active'}>
                <Link to={`/chiptuning/${data.seo_url}`}>
                    <img data-toggle="tooltip" title={data.var_title}
                         src={`https://api.carecusoft.com/userfiles/${data.var_logo_big}`} alt={'Logo'}/>
                </Link>
            </div>
        )
    }

    useEffect(() => {
        try {
            instance
                .get("/chiptuning")
                .then(result => setLogos(result.data))
        }catch (e) {

        }

    }, []);
    return (
        <div>
            <CstLogos logos={logos}/>
        </div>
    );

}


export default withRouter(Main);