import React, {useContext, useEffect, useState} from 'react';
import {withRouter} from 'react-router-dom';
import instance from '../apis/chip';
import LoadGif from '../assets/img/load.gif';
import ToLink from "./ToLink";
import ToBack from "./ToBack";
import LogoView from "./LogoView";
import {BreadContext, EngineContext, EnginesContext, ModelContext, TitleContext, YearContext} from "../Store";
import Bread from "./Bread";

function Models({match}) {
    const [engines, setEngines] = useContext(EnginesContext);

    const [, setTitle] = useContext(TitleContext);
    const [year, setYear] = useContext(YearContext);
    const [, setModel] = useContext(ModelContext);
    const [, setBread] = useContext(BreadContext)
    const [load, setLoad] = useState(true)

    function Items() {
        if (load === true) {
            return (
                <img src={LoadGif} alt={'Loading'}/>
            )
        }
        try {
            return engines.map((data) =>
                <ToLink key={data.id}
                        to={`/chiptuning/${data.seo_url}`}
                        title={`${data.var_title} (${data.int_hp}Hp)`}/>
            )
        } catch (e) {
            return (
                <img src={LoadGif} alt={'Loading'}/>
            )
        }
    }

    useEffect(() => {
        instance
            .get(`/chiptuning/${match.params.brandId}/${match.params.modelId}/${match.params.yearId}`)
            .then(result => {
                setEngines(result.data)
                const yearData = result.data[0].parent
                const modelData = yearData.parent
                const brandData = modelData.parent
                setTitle(yearData.seo_title)
                setYear(yearData)
                setModel(modelData)
                setLoad(false)
                setBread([
                    {
                        id: 3,
                        icon: 'calendar-alt',
                        link: `/chiptuning/${yearData.seo_url}`,
                        title: yearData.var_title
                    },
                    {
                        id: 2,
                        icon: 'copyright',
                        link: `/chiptuning/${modelData.seo_url}`,
                        title: modelData.var_title
                    },
                    {
                        id: 1,
                        icon: 'angle-double-right',
                        link: `/chiptuning/${brandData.seo_url}`,
                        title: brandData.var_title
                    }
                ])
            });
    }, []);

    return (
        <div>
            <Bread/>
            <LogoView brandId={match.params.brandId}/>
            <ToBack to={`/chiptuning/${match.params.brandId}/${match.params.modelId}`} title={year.var_title}/>
            <Items/>
        </div>
    );
}

export default withRouter(Models);