import React, {useContext, useEffect} from 'react';
import {LogoDataContext, BrandIdContext, TitleContext, BreadContext, ModelContext} from '../Store';
import {Link} from "react-router-dom";
import logoGif from '../assets/img/load.gif';
import instance from '../apis/chip';

const LogoView = (props) => {
    const [logoData, setLogoData] = useContext(LogoDataContext)
    const [brandId] = useContext(BrandIdContext)
    const [title] = useContext(TitleContext)
    const [, setBrandId] = useContext(BrandIdContext)

    document.title = `${title} Chip Tuning`;
    if (brandId != props.brandId) {
        useEffect(() => {
            instance
                .get(`/brand/logo/${props.brandId}`)
                .then(result => {
                    setLogoData(`https://api.carecusoft.com/userfiles/${result.data}`)
                });
        }, []);
        setBrandId(props.brandId)
    }
    const logoImg = () => {
        if (logoData === null) {
            return <img src={logoGif} alt={'Logo'}/>
        } else {
            return <img src={logoData} alt={'Logo'}/>
        }
    }
    return (
        <>
            <Link to={`/#/`}>
                <div id="cst-brands-title" className="cst-brands-title cst-brands">
                    {logoImg()}
                </div>
            </Link>
        </>
    );
}

export default LogoView;