import React, {useContext, useEffect, useState} from 'react';
import {withRouter} from 'react-router-dom';
import instance from '../apis/chip';

import LoadGif from "../assets/img/load.gif";
import ToLink from './ToLink';
import ToBack from "./ToBack";
import LogoView from './LogoView';
import Bread from "./Bread";

import {BrandIdContext, BreadContext, ModelsContext, TitleContext} from "../Store";

function Models({match}) {
    const [models, setModels] = useContext(ModelsContext);
    const [, setTitle] = useContext(TitleContext);
    const [, setBrandId] = useContext(BrandIdContext)
    const [, setBread] = useContext(BreadContext)
    const [load, setLoad] = useState(true)

    function Items() {
        if (load === true) {
            return (
                <img src={LoadGif} alt={'Loading'}/>
            )
        }
        try {
            return models.map((data) =>
                <ToLink key={data.id} to={`/chiptuning/${data.seo_url}`} title={data.var_title}/>
            )
        } catch (e) {
            return (
                <img src={LoadGif} alt={'Loading'}/>
            )
        }
    }

    useEffect(() => {
        instance
            .get(`/chiptuning/${match.params.brandId}`)
            .then(result => {
                setModels(result.data)
                setTitle(result.data[0].parent.var_title)
                setBrandId(match.params.brandId)
                setBread([
                    {
                        id: 1,
                        icon: 'angle-double-right',
                        link: `/chiptuning/${result.data[0].parent.seo_url}`,
                        title: result.data[0].parent.var_title
                    },

                ])
                setLoad(false)
            });
    }, []);
    try {
        return (
            <>
                <Bread/>
                <LogoView brandId={match.params.brandId}/>
                <ToBack to={`/`} title={'Cars'}/>
                <Items/>
            </>
        );
    } catch (e) {
        return <img src={LoadGif} alt={'Loading'}/>
    }

}

export default withRouter(Models);