import React, {useContext} from 'react';
import {Link} from "react-router-dom";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {LangContext} from "../Store";

function ToLink(props) {
    const [lang] = useContext(LangContext)

    function convert(str) {
        if (str !== '' && str !== undefined) {
            const a = str.replace('Series', lang.series);
            const b = a.replace('All Years', lang.all_years)
            const c = b.replace('Cars', lang.cars)
            return c
        } else {
            return str;
        }
    }

    const title = convert(props.title)
    return (<Link onClick={props.click} to={props.to}>
        <div className={`cst-item-text ${props.active == true ? 'cst-item-text-active' : ''}`}>
            <div className={'cst-item-text-text'}>{title}</div>
            <div className="cst-item-text-logo">
                <FontAwesomeIcon icon="angle-double-right"/>
            </div>
        </div>
    </Link>)
}

export default ToLink;