import React, {useEffect, useContext} from 'react';
import {HashRouter, Route, Switch} from "react-router-dom";
import Main from './components/Main';
import Models from './components/Models';
import Years from './components/Years';
import Engines from './components/Engines';
import Stages from './components/Stages';
import {library} from '@fortawesome/fontawesome-svg-core'
import {faAngleDoubleLeft, faAngleDoubleRight, faHome, faCar, faCalendarAlt, faCog, faMeteor, faCopyright} from '@fortawesome/free-solid-svg-icons'
import instance from "./apis/chip";
import {LangContext} from "./Store";

library.add(faAngleDoubleRight, faAngleDoubleLeft, faHome, faCar, faCalendarAlt, faCog, faMeteor, faCopyright)
const App = () => {
    const [, setLang] = useContext(LangContext)
    useEffect(() => {
        instance
            .get('/languages')
            .then(result => {
                setLang(result.data)
            });
    }, []);
    return (
        <div className="App">
            <HashRouter>
                <Switch>
                    <Route exact path={'/'} component={Main}/>
                    <Route exact path={'/chiptuning/:brandId'} component={Models}/>
                    <Route exact path={'/chiptuning/:brandId/:modelId'} component={Years}/>
                    <Route exact path={'/chiptuning/:brandId/:modelId/:yearId'} component={Engines}/>
                    <Route exact path={'/chiptuning/:brandId/:modelId/:yearId/:engineId'} component={Stages}/>
                </Switch>
            </HashRouter>
        </div>
    );
}

export default App;
